.p{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 5px solid rgba(243, 242, 242);
    border-radius: 10px 10px 0 0;
    overflow: hidden;
    background: #f2f2f2;
    /* outline: none; */
}

.p-browser{
    height: 20px;
    background: rgba(243, 242, 242);
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
    /* border: 1px rgba(243, 242, 242) solid; */
}

.p-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background: #fff;
}

.p-img{
    width: 100%;
    transition: all 10s ease;
    
}

.p:hover .p-img{
    transform: translateY(-100%)
}


@media screen and (max-width: 480px) {
    .p{
        width: 40%;
        height: 20vh;
    }   
}