.c{
    height: 100vh;
    position: relative;
}

.c-bg{
    width: 15px;
    height: 100%;
    background-color: #59b256;
    position: absolute;
}

.c-wrapper{
    display: flex;
    padding: 50px;
}

.c-left{
    flex: 1;
}

.c-title{
    font-size: 60px;
    width: 80%;
}

.c-info-item{
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 70%;
}

.c-icon{
    width: 30px;
    height: 30px;
    margin-right: 20px;
}


.c-right{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.c-desc{
    font-weight: 200;
}

form{
    margin-top: 20px;
}

input{
    width: 51%;
    height: 50px;
    border: none;
    border-bottom: 1px solid #000;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
}

:is(input, textarea):focus{
    outline: none;
}

button{
    border: none;
    padding: 10px;
    background: #59b256;
    border-radius: 5px;
    cursor: pointer;
    color: #fff;
    font-weight: 500;
}

@media screen and (max-width: 480px) {
    .c-wrapper{
        flex-direction: column;
        padding: 0px 50px;
    }
    
    .c-title{
        font-size: 30px;
    }

    .c-info-item{
        margin: 20px 0px;
        width: 100%;
    }

    .c-desc{
        display: none;
    }

    form{
        margin-top: 0px;
        display: flex;
        flex-wrap: wrap;
    }

    input{
        width: 100%;
        margin: 10px 0;
    }
}

@media screen and (max-width: 340px) {
    .c-title{
        font-size: 25px;
    }
}